<template>
  <div>
    <wh-screenfull ref="test" @getFullScreenStatus="getFullScreenStatus">
      <template slot="content">
        <div class="btn" @click="clickFullscreen()">{{ isFullscreen ? '退出全屏' : '全屏' }}</div>
      </template>
    </wh-screenfull>
  </div>
</template>
<script>
export default {
  name: 'screenfull',
  data() {
    return {
      isFullscreen: false,
    }
  },
  mounted() {
  },
  methods: {
    getFullScreenStatus(status) {
      this.isFullscreen = status
    },
    clickFullscreen() {
      this.$refs.test.toggleFullscreen()
    },
  }
}
</script>
<style scoped>
 .btn{
   color: white;
   cursor: pointer;
 }
</style>



